import React, { Component } from "react";
import styled from "styled-components";
import DetailTemp from "@demoComponents/DetailTemp";
import ApiTable from "@demoComponents/ApiTable";
import CodeDemo from "@demoComponents/CodeDemo";
import Demo1 from "./Demo1";
import Demo2 from "./Demo2";

const Description = styled.section``;
const Column = styled.div``;
const Apis = styled.section`
  padding-top: 20px;
  width: 100%;
  clear: both;
  table {
    width: 100%;
  }
  td {
    padding: 4px 6px;
  }
`;

class Header extends Component {
  render() {
    const demoList = [
      {
        display: <Demo1 />,
        description: "",
        code: `import { Header } from "@es";

class Demo1 extends Component {
  changeHandle = () => {
    alert("");
  };
  render() {
    return (
      <Header
        title={""}
        user={{
          name: "",
          number: "10086"
        }}
        onChange={this.changeHandle}
      >
        
      </Header>
    );
  }
}`
      },
      {
        display: <Demo2 />,
        description: "header",
        code: `class Demo2 extends Component {
  companyChange = (id, content) => {
    alert("id" + id + "");
  };
  render() {
    return (
      <Header
        user={{
          name: "",
          number: "10086"
        }}
        companyList={[
          { content: "1", id: "1" },
          { content: <span>2</span>, id: "2" }
        ]}
        defaultCompany={'1'}
        onCompanyChange={this.companyChange}
      />
    );
  }
}`
      }
    ];

    return (
      <DetailTemp title={"Header"}>
        <Description>
          <h2></h2>
          <p>
            1. <br />
            2. layout  nav
          </p>
        </Description>
        <Column className={"demo-column"}>
          {demoList
            .filter((ele, index) => index % 2 === 0)
            .map((ele, index) => (
              <CodeDemo
                display={ele.display}
                description={ele.description}
                key={index}
              >
                {ele.code}
              </CodeDemo>
            ))}
        </Column>
        <Column className={"demo-column"}>
          {demoList
            .filter((ele, index) => index % 2 === 1)
            .map((ele, index) => (
              <CodeDemo
                display={ele.display}
                description={ele.description}
                key={index}
              >
                {ele.code}
              </CodeDemo>
            ))}
        </Column>
        <Apis>
          <h2>Api</h2>
          <ApiTable
            data={[
              {
                name: "className",
                description: " class",
                type: "string",
                default: ""
              },
              {
                name: "defaultStyles",
                description: "",
                type: "string",
                default: ""
              },
              {
                name: "children",
                description: "",
                type: "node",
                default: ""
              },
              {
                name: "logo",
                description: "logo",
                type: "node",
                default: ""
              },
              {
                name: "title",
                description: "",
                type: "node",
                default: ""
              },
              {
                name: "newsCount",
                description: "",
                type: "number",
                default: ""
              },
              {
                name: "user",
                description: " {name, number}",
                type: "object",
                default: ""
              },
              {
                name: "theme",
                description: " {mainColor, fontColor}",
                type: "object",
                default: ""
              },
              {
                name: "isblur",
                description: "",
                type: "bool",
                default: ""
              },
              {
                name: "onChange",
                description: "",
                type: "function",
                default: ""
              },
              {
                name: "defaultValue",
                description: "",
                type: "bool",
                default: ""
              },
              {
                name: "company",
                description: " id",
                type: "string",
                default: ""
              },
              {
                name: "defaultCompany",
                description: " id",
                type: "string",
                default: ""
              },
              {
                name: "companyList",
                description: " `[{id, content}]`",
                type: "array",
                default: ""
              },
              {
                name: "onCompanyChange",
                description: " params: `(id, content)`",
                type: "function",
                default: ""
              }
            ]}
          />
        </Apis>
      </DetailTemp>
    );
  }
}

export default Header;
